	export default {
		data() {
			return {
				userInfo: {},
				token:''
			}
		},
		computed:{
			login(){
				if(this.token != ""){
					return true;
				}else{
					return false;
				}
			},
			avatarUrl(){
				if(this.token != ""){
					return '../../static/images/avater.png'
				}else{
					return '../../static/images/nologin.png'
				}
			},
			vipIcon(){
				if(this.userInfo.vip_end_time != null){
					return '../../static/images/isvip.png'
				}else{
					return '../../static/images/novip.png'
				}
			},
			endDate(){
				if(this.userInfo.vip_end_time != null){
					var date = new Date().getTime();
					date = Math.floor(date/1000)+this.userInfo.vip_end_time
					date = date*1000;
					var newDate = new Date(date)
					if(newDate.getFullYear() >= 2050){
						return '永久';
					}
					return newDate.getFullYear() + "-" + (newDate.getMonth() + 1) + "-" + newDate.getDate();
				}else if(this.login){
					return '开通VIP会员'
				}else{
					return '-';
				}
			}
		},
		onLoad() {
// 			uni.getStorage({
// 				key: 'TOKEN',
// 				success: (res)=> {
// 					this.token = res.data
// 				}
// 			});
		},
		onShow() {
			var value = uni.getStorageSync('TOKEN');
			if (value) {
				this.token = value;
				this.getUserInfo();
			}else{
				this.token = '';
				this.userInfo = {}
			}
		},
		methods: {
			toLogin(){
				var loginRes = this.$checkLogin('/pages/mine/mine',2);
				if(!loginRes){return false;}
			},
			toUserInfo(){
				var loginRes = this.$checkLogin('/pages/mine/mine',2);
				if(!loginRes){return false;}
				uni.navigateTo({
					url:"/pages/userInfo/userInfo?info="+JSON.stringify(this.userInfo)
				})
			},
			toRecharge(){
				var loginRes = this.$checkLogin('/pages/mine/mine',2);
				if(!loginRes){return false;}
// 				uni.navigateTo({
// 					url:"/pages/recharge/recharge"
// 				})
				uni.navigateTo({
					url:'/pages/downApp/downApp'
				})
			},
			toInvite(){
				var loginRes = this.$checkLogin('/pages/mine/mine',2);
				if(!loginRes){return false;}
				uni.navigateTo({
					url:"/pages/invite/invite?num="+this.userInfo.mobile
				})
			},
			toSecurity(){
				var loginRes = this.$checkLogin('/pages/mine/mine',2);
				if(!loginRes){return false;}
				uni.navigateTo({
					url:"/pages/security/security?num="+this.userInfo.mobile
				})
			},
			toFeedback(){
				var loginRes = this.$checkLogin('/pages/mine/mine',2);
				if(!loginRes){return false;}
				uni.navigateTo({
					url:"/pages/feedback/feedback"
				})
			},
			toSetting(){
				var loginRes = this.$checkLogin('/pages/mine/mine',2);
				if(!loginRes){return false;}
				uni.navigateTo({
					url:"/pages/setting/setting"
				})
			},
			getUserInfo(){
				this.$uniRequest(
					this.$baseUrl+'/v1/member/info',
					0,
					(res)=>{
						this.userInfo = res.data.data;
					}
				)
			}
		}
	}
